.navbar{
    background-color:#333;
    color:white;
    padding:10px 20px;
    position:fixed;
    width:100%;
    top:0;
    z-index:10;
    display:flex;
    justify-content:space-between;
    align-items:center;
    box-shadow: 0 2px 5px rgba(0,0,0,0.2);

}

.navbar-container{
    display:flex;
    justify-content: space-between;
    align-items: center;
    width:100px;
}

.navbar-logo a{
    text-decoration: none;
    color:white;
    font-size:1.5rem;
    font-size:bold;

}
.navbar-links{
    display:flex;
    list-style: none;
    gap:20px;
    margin:0;
}

.navbar-links li a{
    text-decoration: none;
    color:white;
    font-size:bold;
    transition:color 0.3s ease;
}
.navbar-links li a:hover{
    color:blue;
    text-decoration: underline;
}
.navbar-links li a:visited {
    color: green; /* Change this to your desired visited link color */
  }

.navbar-toggle{
    display:none;
    font-size:1.5rem;
    background:none;
    border:none;
    color:white;
    cursor:pointer;
}

@media screen and (max-width:768px){
    .navbar-links{
        display:none;
    

    }

    .navbar-links.active{
        display:flex;
    }

    .navbar-toggle{
        display:block;
    }

